@import '../../../styles/colors';

.sub-menu {
    position: absolute;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    width: 200px;
    left: 0;
    top: 100%;
    background-color: $deepBlackShadow;
    border-top: 3px solid $goldColor;
    transform: translateY(15px);
    transition: transform .3s ease, opacity .5s ease;
    opacity: 0;
    visibility: hidden;


    .menu-item {
      display: block;
    }
  }