@import '../../styles/colors';
@import '../../styles/size';
@import '../../styles/animate.css';
@import '../../styles/mixin';

.hero {
  position: relative;
  height: 100vh;
  width: 100%;
  color: #ddd;
  letter-spacing: 2px;
  text-transform: uppercase;
  overflow: hidden;

  &__img {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url('../../img/small/home1.jpg');
    background-position: center;
    background-size: cover;
    background-attachment: scroll;
    font-size: 10px;
    z-index: 0;
    
    &--shadow {
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: $blackShadow;
      z-index: -1;
    }
  }

  &__arrow {
    position: absolute;
    color: $goldColor;
    margin-left: calc(50% - 25px);
    padding: 15px 15px 0 15px;
    font-size: 3em;
    margin-left: 50%;
    bottom: 0;
    text-shadow: 0 0 8px $goldColor;
    animation: bounce 1.6s infinite;
    cursor: pointer;
    z-index: 1;
  }

  &__text {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0 30px;
    width: 100%;
    height: 100%;
    transition: margin-left 1s;
    opacity: 0;
    z-index: 1;

    h1 {
      @include heroTextBounce(1.6s, 0.3s, 0);
    }

    p {
      @include heroTextBounce(1.6s, 0.5s, 0.6em);
    }

    button {
      @include heroTextBounce(1.6s, 0.8s, 2em);
      font-size: 10px;
      padding: 10px;
      text-decoration: none;
      letter-spacing: 1px;
      color: white;
      text-transform: uppercase;
      background: none;
      border: 1px solid white;
      border-radius: 5px;
      cursor: pointer;
      transition: color, background-color 0.3s;

      &:hover {
        background-color: $whiteShadow;
        color: $goldColor;
      }

      &:active {
        background-color: aqua;
      }
    }
  }
}
