@import '../../styles/size';
@import '../../styles/colors';

.header {
  position: fixed;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 99;
//   background-color: $deepBlackShadow;
  z-index: 10;

  .container {
    max-width: 1170px;
    margin: auto;

    .header-main {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 10px 0;

      &__logo {
        height: 80px;
        width: 80px;
        z-index: 10;
        svg {
          fill: $goldColor;
        }
      }
      .nav-social-menu {
        display: flex;
        align-items: center;
      }
    }
  }
}
