#contact {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: darkslategray;
    padding: 50px 30px;
}
.contact-form {
    display: flex;
    flex-direction: column;
    width: 80%;
    max-width: 1000px;
    background-color: burlywood;
    padding: 20px;

    &__input {
        width: 100%;
        margin: 15px 0;
    }
}