@import '../../../styles/colors';

.nav-menu {
  padding: 0 15px;

  .menu {
    list-style: none;
    margin: 0;
    padding: 0;

    .menu-item {
      display: inline-block;
      position: relative;
      margin-left: 10px;

      &:hover .sub-menu {
        transform: translateY(0);
        opacity: 1;
        visibility: visible;
      }
    }
  }
}
