@mixin heroTextBounce($bounceTime, $bounceDelay, $marginBottom) {
  text-align: center;
  margin-top: $marginBottom;
  animation: bounceInUp $bounceTime $bounceDelay;
}

@mixin navLine($scale) {
  width: 3px;
  height: 100%;
  background-color: $goldColor;
  transform: scaleY($scale);
  transition: transform 0.3s;
  content: '';
}

@mixin whiteBlock($bgc) {
  background-color: $bgc;
  transform: skew(45deg);
}

@mixin headerImg($img) {
  background-image: url($img);
  background-position: center;
  background-size: cover;
  background-attachment: fixed;
  min-height: 320px;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 400px;
}

@mixin navItem {
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: $pureWhite;
  padding: 10px;
  margin: 5px;
  transition: color 0.3s;
  
  &:hover {
    color: $goldColor;
  }
}
