@import 'colors';
@import 'size';

@media (min-width: 576px) {
  //HEADER
}

//HEADER
@media (min-width: 768px) {
  //FOOTER
  .footer {
    &__boxes {
      display: flex;
      justify-content: space-around;
    }

    &__box {
      &--desktop {
        display: block;
      }

      &:first-child {
        width: 50%;
      }

      &--title {
        font-size: 2.4em;
      }
    }
  }
}

@media (max-width: 991px) {
  //HEADER
  .header {

    .nav-menu {
      position: fixed;
      background-color: black;
      top: 0;
      left: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100vh;
      overflow-y: auto;

      .menu-item {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        .nav-item:hover {
          background-color: rgba(255,255,255,0.06);
        }

        &:hover .sub-menu {
          max-height: none;

        }

        &:focus .sub-menu {
          max-height: none;
        }
        .sub-menu {
          display: flex;
          flex-direction: column;
          align-items: center;
          justify-content: center;
          width: 100%;
          position: relative;
          max-height: 0;
          overflow: hidden;
          border-bottom: 3px solid $goldColor;
          .nav-item:hover {
            background-color: rgba(255,255,255,0.06);
          }
        }
      }
    }
  }
}
