@import '../../styles/colors';
@import '../../styles/size';

.social {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: display 0.3s;
  z-index: 10;
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    height: $wMenu;
    width: $wMenu;
    margin: $marginSocial;

    &--font {
      font-size: ($wMenu - $marginSocial) - 8px;
      color: $pureWhite;
      transition: color 0.35s;
    }
    &:hover {
      .social__item--font {
        color: $goldColor;
      }
    }
  }
}
