@import '../../../styles/colors';

.nav-item {
  color: $pureWhite;
  display: block;
  text-decoration: none;
  text-transform: uppercase;
  letter-spacing: 2px;
  color: $pureWhite;
  text-shadow: 1px 1px 1px  black;
  padding: 15px;
  transition: color 0.3s;

  &:hover {
    color: $goldColor;
  }
  &:focus {
    color: aqua;
  }
}
